

import { mapStores } from 'pinia';
import Vue from 'vue';

import useSettingsStore from '@/store/SettingsStore';
import Base from '@/views/Settings/Base.vue';
import Utils, { PlayerUtils } from '@/utils';

export default Vue.extend({
    name: 'Settings-General',
    components: {
        Base,
    },
    data() {
        return {

            // 
            PlayerUtils: PlayerUtils,

            // 
            is_form_dense: Utils.isSmartphoneHorizontal(),

            // 
            tv_streaming_quality: [
                {'text': '1080p (60fps) 3.24GB/h / 7.2Mbps', 'value': '1080p-60fps'},
                {'text': '1080p 2.31GB/h / 5.1Mbps', 'value': '1080p'},
                {'text': '810p 1.92GB/h / 4.2Mbps', 'value': '810p'},
                {'text': '720p 1.33GB/h / 3.0Mbps', 'value': '720p'},
                {'text': '540p 1.00GB/h / 2.2Mbps', 'value': '540p'},
                {'text': '480p 0.74GB/h / 1.6Mbps', 'value': '480p'},
                {'text': '360p 0.40GB/h / 0.9Mbps', 'value': '360p'},
                {'text': '240p 0.23GB/h / 0.5Mbps', 'value': '240p'},
            ],

            // 
            panel_display_state: [
                {'text': '', 'value': 'RestorePreviousState'},
                {'text': '', 'value': 'AlwaysDisplay'},
                {'text': '', 'value': 'AlwaysFold'},
            ],

            // 
            tv_panel_active_tab: [
                {'text': '', 'value': 'Program'},
                {'text': '', 'value': 'Channel'},
                {'text': '', 'value': 'Comment'},
                {'text': 'Twitter ', 'value': 'Twitter'},
            ],

            // 
            caption_font: [
                {'text': 'Windows TV ', 'value': 'Windows TV Gothic'},
                {'text': 'Windows TV ', 'value': 'Windows TV MaruGothic'},
                {'text': 'Windows TV ', 'value': 'Windows TV FutoMaruGothic'},
                {'text': 'TV', 'value': 'Hiragino TV Sans Rd S'},
                {'text': ' ARIB', 'value': 'TT-ShinMGo-regular'},
                {'text': 'Rounded M+ 1m for ARIB', 'value': 'Rounded M+ 1m for ARIB'},
                {'text': 'Noto Sans JP', 'value': 'Noto Sans JP Caption'},
                {'text': '', 'value': 'sans-serif'},
            ],

            // 
            capture_save_mode: [
                {'text': '', 'value': 'Browser'},
                {'text': 'KonomiTV ', 'value': 'UploadServer'},
                {'text': 'KonomiTV ', 'value': 'Both'},
            ],

            // 
            capture_caption_mode: [
                {'text': '', 'value': 'VideoOnly'},
                {'text': '', 'value': 'CompositingCaption'},
                {'text': '', 'value': 'Both'},
            ],

            //  (KonomiTV-Settings.json) 
            import_settings_file: null as File | null,
        }
    },
    computed: {
        // SettingsStore  this.settingsStore 
        // ref: https://pinia.vuejs.org/cookbook/options-api.html
        ...mapStores(useSettingsStore),
    },
    methods: {

        // 
        exportSettings() {

            //  JSON 
            const settings_json = JSON.stringify(this.settingsStore.settings, null, 4);

            //  Blob KonomiTV-Settings.json 
            const settings_json_blob = new Blob([settings_json], {type: 'application/json'});
            Utils.downloadBlobData(settings_json_blob, 'KonomiTV-Settings.json');
            this.$message.success('');
        },

        // 
        async importSettings() {

            // 
            if (this.import_settings_file === null) {
                this.$message.error('');
                return;
            }

            // 
            const result = await this.settingsStore.importClientSettings(this.import_settings_file);
            if (result === true) {
                this.$message.success('');
                window.setTimeout(() => this.$router.go(0), 300);
            } else {
                this.$message.error('');
            }
        },

        // 
        async resetSettings() {
            await this.settingsStore.resetClientSettings();
            this.$message.success('');
            window.setTimeout(() => this.$router.go(0), 300);
        },
    }
});

